body{
  background-color: rgb(255, 246, 228);
}

input{
  width:100%;
  padding:12px 20px;
  margin:4px 0;
  display:inline-block;
  border:1px solid #ccc;
  border-radius:4px;
  box-sizing:border-box;
}

.todo-app{
  margin:20px 300px;
}

.main{
  width:100%;
  height:100%;
  display:flex;
  flex-direction:column;
  align-items:center;
  justify-content:center;
}

button{
  background-color: #4285F4;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  /* width: 100%; */
}
.todo-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 4px 0px;
  /* color: #fff; */
  background: white;

  padding: 16px;
  border-radius: 5px;
  width: 97%;
}

.icons {
  display: flex;
  align-items: center;
  font-size: 35px;
  cursor: pointer;
  /* font-weight: lighter; */
}


.complete-item{
 
  color: rgb(144, 255, 144);
  
}
.delete-item{
  margin-right: 5px;
  color: rgb(250, 115, 115);
}

.complete {
  text-decoration: line-through;
  opacity: 0.7;
}

@media screen and (max-width: 700px) {
  .todo-app {
    margin:20px 50px;
  }
  .todo-row {
   
    width: 90%;
  }
  
}